* {
  padding: 0;
  margin: 0;
}
.image-list {
  padding: 0 0 15px 0;
  background-color: #fff;
}
.list-title {
  padding: 0 10px;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #f3f3f3;
}
.more {
  float: right;
}
.list-title span a {
  font-size: 12px;
  color: #999;
}
.list-title h2 {
  font-size: 16px;
  color: #000;
}
.image-list li {
  float: left;
  width: 46%;
  margin-left: 2.5%;
  margin-top: 2.5%;
  height: 130px;
  min-height: 130px;
  position: relative;
  overflow: hidden;
}
.image-list a {
  color: #fff;
}
.image-list li img {
  width: 100%;
  height: 130px;
  border-radius: 3px;
}
.image-list li span {
  width: 100%;
  background-color: #000;
  opacity: 0.3;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 30px;
  text-indent: 5px;
  line-height: 30px;
  border-radius: 0 0 3px 3px;
  overflow: hidden;
}
.image-list li em {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 30px;
  text-indent: 5px;
  line-height: 30px;
  overflow: hidden;
  text-align: center;
  font-style: normal;
}
