.bottom-navigation {
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
  background: #fcfcfc;
  border-top: 1px solid #ececec;
  z-index: 10;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.bottom-navigation a {
  width: 20%;
  height: 100%;
  padding-top: 14px;
  line-height: 9px;
  text-align: center;
  float: left;
  color: #666;
  font-size: 12px;
}
.bottom-navigation i {
  font-size: 22px;
  display: block;
}
.bottom-navigation .selected {
  color: #41c2fc;
}
.bottom-navigation .publish {
  width: 20%;
  padding-top: 0;
  margin-top: -10px;
  line-height: 55px;
}
.bottom-navigation .publish i {
  width: 55px;
  height: 55px;
  background: linear-gradient(#8ddcff, #41c2fc 50%, #2aa9e2);
  border: 6px solid #fafbfd;
  border-radius: 5rem;
  font-size: 25px;
  color: #fff;
  margin: 0 auto;
  display: block;
}
.publish-circle {
  width: 63px;
  height: 63px;
  border-radius: 5rem;
  border: 1px solid #dbdbdb;
  z-index: 9;
  position: fixed;
  bottom: -4px;
  display: flex;
  left: calc(50% - 33px);
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
}
