body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s ease;
          animation: App-logo-spin infinite 20s ease;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
  width: 100%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  color: white;
  font-size: 19px;
  z-index: 99;
}
.bg {
  background: #41c2fc;
}
.search {
  position: absolute;
  right: 0;
  top: 0;
  width: 25px;
  height: 45px;
  margin-right: 10px;
  display: block;
  background: no-repeat;
  background-position: 0;
  background-size: 25px auto;
}
.navigation-link {
  position: absolute;
  left: 0;
  top: 0;
  width: 55px;
  height: 45px;
  margin-left: 10px;
  display: block;
}
.navigation {
  padding-left: 10px;
  background: no-repeat;
  background-position: -12px;
  background-size: 30px, auto;
}
.avatar {
  width: 24px;
  height: 24px;
  margin-top: 9px;
  border: 2px solid rgba(255, 255, 255, 0.7);
  border-radius: 5rem;
  position: relative;
}
.avatar b {
  width: 8px;
  height: 8px;
  position: absolute;
  right: -4px;
  top: -3px;
  background: #ff6600;
  border: 1px solid #fff;
  border-radius: 5rem;
}

.nav-menu {
  line-height: 30px;
  padding: 10px 0;
  background-color: #fff;
  margin: 0;
}
.nav-menu a {
  float: left;
  width: 25%;
  font-size: 16px;
  color: #333;
  text-align: center;
}
a:link {
  text-decoration: none;
}
.cl {
  zoom: 1;
}
.cl:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.slide {
  position: relative;
  height: 210px;
}
.slide img {
  display: block;
  width: 100%;
  border: 0 none;
}
.swiper-container {
  height: 210px;
}

.wave {
  position: absolute;
  left: 0;
  bottom: -10px;
  height: 30px;
  width: 100%;
  z-index: 1;
}
.water_1 {
  position: absolute;
  width: 100%;
  height: 30px;
  background-size: 600px;
  -webkit-animation: wave-animation-1 3.5s infinite linear;
          animation: wave-animation-1 3.5s infinite linear;
}
.water_2 {
  position: absolute;
  width: 100%;
  height: 30px;
  background-size: 600px;
  -webkit-animation: wave-animation-2 3.5s infinite linear;
          animation: wave-animation-2 3.5s infinite linear;
}
@-webkit-keyframes wave-animation-1 {
  0% {
    background-position: 0 top;
  }
  100% {
    background-position: 600px top;
  }
}
@keyframes wave-animation-1 {
  0% {
    background-position: 0 top;
  }
  100% {
    background-position: 600px top;
  }
}
@-webkit-keyframes wave-animation-2 {
  0% {
    background-position: 0 top;
  }
  100% {
    background-position: 600px top;
  }
}
@keyframes wave-animation-2 {
  0% {
    background-position: 0 top;
  }
  100% {
    background-position: 600px top;
  }
}

.bottom-navigation {
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
  background: #fcfcfc;
  border-top: 1px solid #ececec;
  z-index: 10;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.bottom-navigation a {
  width: 20%;
  height: 100%;
  padding-top: 14px;
  line-height: 9px;
  text-align: center;
  float: left;
  color: #666;
  font-size: 12px;
}
.bottom-navigation i {
  font-size: 22px;
  display: block;
}
.bottom-navigation .selected {
  color: #41c2fc;
}
.bottom-navigation .publish {
  width: 20%;
  padding-top: 0;
  margin-top: -10px;
  line-height: 55px;
}
.bottom-navigation .publish i {
  width: 55px;
  height: 55px;
  background: linear-gradient(#8ddcff, #41c2fc 50%, #2aa9e2);
  border: 6px solid #fafbfd;
  border-radius: 5rem;
  font-size: 25px;
  color: #fff;
  margin: 0 auto;
  display: block;
}
.publish-circle {
  width: 63px;
  height: 63px;
  border-radius: 5rem;
  border: 1px solid #dbdbdb;
  z-index: 9;
  position: fixed;
  bottom: -4px;
  display: -webkit-flex;
  display: flex;
  left: calc(50% - 33px);
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
}

@font-face {
  font-family: "iconfont";
  src: url(/tanntei-app/static/media/iconfont.8c73432a.eot); /* IE9*/
  src: url(/tanntei-app/static/media/iconfont.8c73432a.eot#iefix) format("embedded-opentype"),
    
      url("data:application/x-font-woff;charset=utf-8;base64,")
      format("woff"),
    url(/tanntei-app/static/media/iconfont.ee87f6a5.ttf) format("truetype"),
    
      url(/tanntei-app/static/media/iconfont.3258f9a9.svg#iconfont) format("svg"); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-home:before {
  content: "\E680";
}
.icon-community:before {
  content: "\E68D";
}
.icon-publish:before {
  content: "\E610";
}
.icon-circle:before {
  content: "\E638";
}
.icon-user-center:before {
  content: "\E626";
}

* {
  font-weight: 300 !important;
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
.drawerLayout {
  width: 280px;
  max-width: 400px;
  height: 100%;
  background-color: #fbfbfb;
  position: fixed;
  z-index: 100;
  top: 0;
}
.drawerLayout .header {
  width: 100%;
  height: 130px;
  background: url(/tanntei-app/static/media/header_bg.fdf387f0.jpg);
  background-size: cover;
  position: relative;
}
.drawerLayout .header .left {
  padding: 27px 10px;
  float: left;
}
.drawerLayout .header .left .user {
  width: 70px;
  height: 70px;
  border: 3px solid rgba(255, 255, 255, 0.4);
  border-radius: 5rem;
}
.drawerLayout .header .right {
  padding-top: 32px;
  float: left;
}
.drawerLayout .header .right .slogan a {
  height: 30px;
  line-height: 30px;
  font-size: 17px;
  color: #fff;
  vertical-align: middle;
  text-shadow: 0 0 1px #000;
}
.drawerLayout .header .right .login_register {
  padding-top: 5px;
}
.drawerLayout .header .right .login_register a {
  padding-left: 25px;
  color: #e4e4e4;
  font-size: 12px;
}
.drawerLayout .header .right .login_register .login {
  width: 55px;
  height: 25px;
  line-height: 25px;
  margin-right: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  float: left;
}
.drawerLayout .header .right .login_register .login .login_btn {
  background: url(data:image/png;base64,) no-repeat;
  background-position: 6px;
  background-size: 15px auto;
}
.drawerLayout .header .right .login_register .register {
  width: 55px;
  height: 25px;
  line-height: 25px;
  margin-right: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  float: left;
}
.drawerLayout .header .right .login_register .register .register_btn {
  background: url(data:image/png;base64,) no-repeat;
  background-position: 6px;
  background-size: 18px auto;
}
.drawerLayout .menu_bar {
  width: 100%;
  height: 65px;
  padding-top: 15px;
  background: #fff;
  border-bottom: 1px solid #eaeaea;
  background-size: cover;
}
.drawerLayout .menu_bar li {
  float: left;
  width: 25%;
  text-align: center;
  position: relative;
}
.drawerLayout .menu_bar img {
  width: 33px;
  height: 33px;
  vertical-align: top;
}
.drawerLayout .menu_bar p {
  line-height: 25px;
  font-size: 14px;
  color: #333;
}

* {
  padding: 0;
  margin: 0;
}
.image-list {
  padding: 0 0 15px 0;
  background-color: #fff;
}
.list-title {
  padding: 0 10px;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #f3f3f3;
}
.more {
  float: right;
}
.list-title span a {
  font-size: 12px;
  color: #999;
}
.list-title h2 {
  font-size: 16px;
  color: #000;
}
.image-list li {
  float: left;
  width: 46%;
  margin-left: 2.5%;
  margin-top: 2.5%;
  height: 130px;
  min-height: 130px;
  position: relative;
  overflow: hidden;
}
.image-list a {
  color: #fff;
}
.image-list li img {
  width: 100%;
  height: 130px;
  border-radius: 3px;
}
.image-list li span {
  width: 100%;
  background-color: #000;
  opacity: 0.3;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 30px;
  text-indent: 5px;
  line-height: 30px;
  border-radius: 0 0 3px 3px;
  overflow: hidden;
}
.image-list li em {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 30px;
  text-indent: 5px;
  line-height: 30px;
  overflow: hidden;
  text-align: center;
  font-style: normal;
}

.bgDiv {
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.6;
  position: fixed;
  z-index: 99;
  bottom: 0;
}

.publish_div {
  bottom: 0;
  left: 0;
  position: fixed;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  transition: -webkit-transform 400ms;
  transition: transform 400ms;
  transition: transform 400ms, -webkit-transform 400ms;
  width: 100%;
  height: 100%;
  z-index: 100;
}

