.wave {
  position: absolute;
  left: 0;
  bottom: -10px;
  height: 30px;
  width: 100%;
  z-index: 1;
}
.water_1 {
  position: absolute;
  width: 100%;
  height: 30px;
  background-size: 600px;
  animation: wave-animation-1 3.5s infinite linear;
}
.water_2 {
  position: absolute;
  width: 100%;
  height: 30px;
  background-size: 600px;
  animation: wave-animation-2 3.5s infinite linear;
}
@keyframes wave-animation-1 {
  0% {
    background-position: 0 top;
  }
  100% {
    background-position: 600px top;
  }
}
@keyframes wave-animation-2 {
  0% {
    background-position: 0 top;
  }
  100% {
    background-position: 600px top;
  }
}
