* {
  font-weight: 300 !important;
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
.drawerLayout {
  width: 280px;
  max-width: 400px;
  height: 100%;
  background-color: #fbfbfb;
  position: fixed;
  z-index: 100;
  top: 0;
}
.drawerLayout .header {
  width: 100%;
  height: 130px;
  background: url(../images/header_bg.jpg);
  background-size: cover;
  position: relative;
}
.drawerLayout .header .left {
  padding: 27px 10px;
  float: left;
}
.drawerLayout .header .left .user {
  width: 70px;
  height: 70px;
  border: 3px solid rgba(255, 255, 255, 0.4);
  border-radius: 5rem;
}
.drawerLayout .header .right {
  padding-top: 32px;
  float: left;
}
.drawerLayout .header .right .slogan a {
  height: 30px;
  line-height: 30px;
  font-size: 17px;
  color: #fff;
  vertical-align: middle;
  text-shadow: 0 0 1px #000;
}
.drawerLayout .header .right .login_register {
  padding-top: 5px;
}
.drawerLayout .header .right .login_register a {
  padding-left: 25px;
  color: #e4e4e4;
  font-size: 12px;
}
.drawerLayout .header .right .login_register .login {
  width: 55px;
  height: 25px;
  line-height: 25px;
  margin-right: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  float: left;
}
.drawerLayout .header .right .login_register .login .login_btn {
  background: url(../images/ic_login.png) no-repeat;
  background-position: 6px;
  background-size: 15px auto;
}
.drawerLayout .header .right .login_register .register {
  width: 55px;
  height: 25px;
  line-height: 25px;
  margin-right: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  float: left;
}
.drawerLayout .header .right .login_register .register .register_btn {
  background: url(../images/ic_register.png) no-repeat;
  background-position: 6px;
  background-size: 18px auto;
}
.drawerLayout .menu_bar {
  width: 100%;
  height: 65px;
  padding-top: 15px;
  background: #fff;
  border-bottom: 1px solid #eaeaea;
  background-size: cover;
}
.drawerLayout .menu_bar li {
  float: left;
  width: 25%;
  text-align: center;
  position: relative;
}
.drawerLayout .menu_bar img {
  width: 33px;
  height: 33px;
  vertical-align: top;
}
.drawerLayout .menu_bar p {
  line-height: 25px;
  font-size: 14px;
  color: #333;
}
