.publish_div {
  bottom: 0;
  left: 0;
  position: fixed;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  transform: translateY(100%);
  -webkit-transition: -webkit-transform 400ms;
  transition: transform 400ms;
  width: 100%;
  height: 100%;
  z-index: 100;
}
