.navbar {
  width: 100%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  color: white;
  font-size: 19px;
  z-index: 99;
}
.bg {
  background: #41c2fc;
}
.search {
  position: absolute;
  right: 0;
  top: 0;
  width: 25px;
  height: 45px;
  margin-right: 10px;
  display: block;
  background: no-repeat;
  background-position: 0;
  background-size: 25px auto;
}
.navigation-link {
  position: absolute;
  left: 0;
  top: 0;
  width: 55px;
  height: 45px;
  margin-left: 10px;
  display: block;
}
.navigation {
  padding-left: 10px;
  background: no-repeat;
  background-position: -12px;
  background-size: 30px, auto;
}
.avatar {
  width: 24px;
  height: 24px;
  margin-top: 9px;
  border: 2px solid rgba(255, 255, 255, 0.7);
  border-radius: 5rem;
  position: relative;
}
.avatar b {
  width: 8px;
  height: 8px;
  position: absolute;
  right: -4px;
  top: -3px;
  background: #ff6600;
  border: 1px solid #fff;
  border-radius: 5rem;
}
