.nav-menu {
  line-height: 30px;
  padding: 10px 0;
  background-color: #fff;
  margin: 0;
}
.nav-menu a {
  float: left;
  width: 25%;
  font-size: 16px;
  color: #333;
  text-align: center;
}
a:link {
  text-decoration: none;
}
.cl {
  zoom: 1;
}
.cl:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
